import { MapboxGLWrapper } from '../../../LabStarter/Starter-Mapbox/MapboxGLWrapper';
import { RendererCanvas } from '../../../../lib'
import * as turf from '@turf/turf';

export class Solution {
    public divHost: HTMLDivElement;
    public mapboxWrapper: MapboxGLWrapper;
    public renderer: Renderer;
    radius = 0.3;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;
        this.divHost.style.position = 'relative';

        this.mapboxWrapper = new MapboxGLWrapper(this.divHost.id);
        this.mapboxWrapper.mouseDown = this.mouseDown
        this.mapboxWrapper.mouseUp = this.mouseUp
        this.mapboxWrapper.mouseClick = this.mouseClick
        this.mapboxWrapper.mouseMove = this.mouseMove

        setTimeout(() => {
            this.renderer = new Renderer(this.divHost, this);
        }, 100);

        const divUI = document.createElement('div')
        divUI.style.position = 'absolute';
        divUI.style.background = '#000'
        divUI.style.padding = '9px'
        this.divHost.appendChild(divUI)

        const divTitle = document.createElement('div')
        divTitle.textContent = 'Controlling the radius'
        divUI.appendChild(divTitle)

        const slider = getSlider('radius', 1, 1000)
        slider.slider.defaultValue = '100';
        slider.slider.oninput = () => {
            const value = parseInt(slider.slider.value) * 0.001;
            this.radius = value
        };
        divUI.appendChild(slider.div)

        const divDownload = document.createElement('div')
        divDownload.textContent = 'Click here to download'
        divUI.appendChild(divDownload)
        divDownload.onclick = () => {
            downloadGeoJson(this.renderer.outGoem, 'dissolved')
        }
    }
    public mouseDown = (e) => {
        // TODO
    }
    public mouseUp = (e) => {
        // TODO
    }
    public mouseDrag = (e) => {
        // TODO
    }
    public mouseClick = (e) => {
        // TODO
        // console.log(e);
        // console.log(e['lngLat']);
        this.renderer.pts.push([e['lngLat']['lng'], e['lngLat']['lat']])
    }
    public mouseMove = (e) => {
        // TODO
    }
    public keyDown(k: KeyboardEvent) {
        // TODO
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}
export class Renderer extends RendererCanvas {
    public app: Solution
    public time: number = 0.0;
    public pts: number[][] = [];
    outGoem: any
    constructor(div: any, app: Solution) {
        super(div);
        this.app = app;
        this.canvas.id = 'mapboxCustom'
        this.canvas.style.position = 'absolute';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';
        this.canvas.style.pointerEvents = 'none';

        // TODO
        // You code goes here for one time operation
        this.pts.push([-71.093161, 42.358871]);

        this.start();
    }
    public mouseDown = (x: number, y: number): void => {

    }
    public mouseUp = (x: number, y: number): void => {

    }
    public mouseDrag = (x: number, y: number): void => {

    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.time += 0.1;

        const ptsList = []

        for (let i = 0; i < this.pts.length; ++i) {
            const pt = this.pts[i];
            const p = this.app.mapboxWrapper.toScreen(pt[0], pt[1]);

            ptsList.push(this.pts[i])

            ctx.beginPath();
            ctx.arc(p[0], p[1], Math.sin(this.time) * 0.5 + 4, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fillStyle = '#0f0';
            ctx.fill();
        }


        let buffers = []
        for (let i = 0; i < ptsList.length; ++i) {
            let pt = turf.point(ptsList[i]);
            let buffered = turf.buffer(pt, this.app.radius, { units: 'miles' });
            buffers.push(buffered);
        }

        let features = turf.featureCollection(buffers);
        renderPolygon(ctx, features, this.app.mapboxWrapper.toScreen, 'rgba(255, 255, 0, 0.25)', true)


        const dissolved = computeDis(features)
        renderPolygon(ctx, dissolved, this.app.mapboxWrapper.toScreen)
        this.outGoem = dissolved;

        const cen = turf.center(dissolved);
        // console.log(cen)

        const cp = this.app.mapboxWrapper.toScreen(cen.geometry.coordinates[0], cen.geometry.coordinates[1]);
        ctx.beginPath();
        ctx.arc(cp[0], cp[1], Math.sin(this.time) * 0.5 + 8, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fillStyle = '#f00';
        ctx.fill();
    }
}


const renderPolygon = (ctx, geom, toScreen, color = '#fff', dash = false) => {

    for (let i = 0; i < geom.features.length; ++i) {

        dash ? ctx.setLineDash([5,5]): ctx.setLineDash([])

        const feature = geom.features[i];
        let sp = toScreen(feature.geometry.coordinates[0][0][0], feature.geometry.coordinates[0][0][1]);
        ctx.beginPath()
        ctx.moveTo(sp[0], sp[1])
        for (let j = 1; j < feature.geometry.coordinates[0].length; ++j) {
            // console.log(feature.geometry.coordinates[0][j])
            sp = toScreen(feature.geometry.coordinates[0][j][0], feature.geometry.coordinates[0][j][1]);
            // console.log(sp)
            ctx.lineTo(sp[0], sp[1]);
        }


        ctx.closePath();
        ctx.strokeStyle = color;
        ctx.stroke();
    }
}

const computeDis = (geom) => {
    const listD = []
    for (let i = 0; i < geom.features.length; ++i) {
        const feature = geom.features[i]
        listD.push(turf.polygon(feature.geometry.coordinates, { combine: 'yes' }))
    }
    const featuresForDis = turf.featureCollection(listD);

    // @ts-ignore
    const dissolved = turf.dissolve(featuresForDis, { propertyName: 'combine' });
    return dissolved
};

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};

export const downloadGeoJson = (collection, name = 'download',) => {
    const link = document.createElement('a');
    link.href = 'data:application/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(collection));
    link.download = `${name}.geojson`;
    link.click();
};
